import React, { useState, useEffect } from 'react';
import Accordion from "./accordion/Accordion";
import SlideWrap from './slide/SlideWrap';
import { accData } from './data/MenuData';
import { NoticeData } from '../common/data/NoticeData';
import { useSelector } from 'react-redux';


const Footer = () => {

    const { comm, loading } = useSelector(state => state.commReducer)
    
	return (
		<footer id="footer">
       
        {/*  */}
        <section className="footer_sec share">
            <div className="g_inner">
                <ul>
                    <li className="txt">Share</li>
                    <li><a href="http://www.facebook.com/sharer/sharer.php?u=https%3A%2F%2Fwww.genesis.com%2Fkr%2Fko%2Fmain.html" role="button" target="_blank" title="" rel="noreferrer">
                        <i className="fa-brands fa-square-facebook"></i>
                        <span className="blind"></span>
                    </a></li>
                    <li><a href="https://twitter.com/i/flow/login" role="button" target="_blank" title="" rel="noreferrer">
                        <i className="fa-brands fa-twitter"></i>
                        <span className="blind"></span>
                    </a></li>
                    <li><a href="#" role="button" target="_blank" title="" rel="noreferrer">
                        <i className="fa-solid fa-link"></i>
                        <span className="blind"></span>
                    </a></li>
                </ul>
            </div>
        </section>
     
        {/*  */}
        <section className="footer_sec notice">
            <SlideWrap data={comm} defaultText={"[]"} />
        </section>


        <section className="footer_sec all_menu">
            <Accordion 
                data={accData.filter((_, idx) => idx < 4)} 
                className={"type1 g_inner"}
                device={"accordion_mo"}
            />
        </section>
        {/*  */}
        <section className="footer_sec notice">
            <SlideWrap data={NoticeData} defaultText={"[]"} />
        </section>


        <section className="footer_sec sitemap mid">
            <div className="g_inner">
                <ul className="item btw_line">
                    <li><a href="https://www.genesis.com/kr/ko/sitemap.html" role="button" target="_blank" title="" rel="noreferrer"></a></li>
                    <li><a href="https://www.genesis.com/kr/ko/terms-of-use/terms-conditions/website.html" role="button" target="_blank" title="" rel="noreferrer"></a></li>
                    <li><a href="https://www.genesis.com/kr/ko/terms-of-use/privacy-policy/website-member.html" role="button" target="_blank" title="" className="point_c_oran" rel="noreferrer"> </a></li>
                    <li><a href="https://www.genesis.com/kr/ko/terms-of-use/copyright.html" role="button" target="_blank" title="" rel="noreferrer"> </a></li>
                    <li><a href="https://www.genesis.com/kr/ko/terms-of-use/warranty/gv80.html" role="button" target="_blank" title="" rel="noreferrer"></a></li>
                    <li><a href="https://www.genesis.com/kr/ko/members/vehicle-management-service/lemonlaw-rules.html" role="button" target="_blank" title="" rel="noreferrer">      </a></li>
                    <li><a href="https://www.genesis.com/kr/ko/members/vehicle-management-service/lemonlaw.html" role="button">   </a></li>
                </ul>
                <ul className="item sns">
                    <li><a href="https://www.facebook.com/GenesisWorldwide" role="button" target="_blank" title="" rel="noreferrer">
                        <i className="fa-brands fa-square-facebook"></i>
                        <span className="blind"></span>
                    </a></li>
                    <li><a href="https://www.instagram.com/genesis_worldwide/" role="button" target="_blank" title="" rel="noreferrer">
                        <i className="fa-brands fa-instagram"></i>
                        <span className="blind"></span>
                    </a></li>
                    <li><a href="https://www.youtube.com/genesisworldwide" role="button" target="_blank" title="" rel="noreferrer">
                        <i className="fa-brands fa-youtube"></i>
                        <span className="blind"></span>
                    </a></li>
                </ul>
            </div>
        </section>
        
        <section className="footer_sec btm line_black_long">
            <p className="copyright">
                 Copyright 2023 Hyundai Motor Company.<br />All Rights Reserved.
            </p>
        </section>
    </footer>
	);
}

export default Footer;
